"use client";
"use client";

import React from "react";
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Button,
  IconButton,
  TextField,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  Pagination,

  Container,
} from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";


const ProductPage = () => {
  const [category, setCategory] = React.useState("");

  const handleCategoryChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setCategory(event.target.value as string);
  };

  return (
    <>
      <Header />
      <Container sx={{ py: 4, minHeight: "calc(100vh - 120px)", color: "#1b1b2f" }}>
        {/* Page Title and Add Button */}
        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", mb: 4 }}>
          <Typography variant="h4" sx={{ fontWeight: 600, color: "#1b1b2f" }}>
            Manage Products
          </Typography>
          <Button
            variant="contained"
            startIcon={<AddCircleOutlineIcon />}
            sx={{
              backgroundColor: "#1f4068",
              color: "#e0e0e0",
              textTransform: "none",
              "&:hover": { backgroundColor: "#162447" },
            }}
          >
            Add Product
          </Button>
        </Box>

        {/* Filters Section */}
        <Box sx={{ display: "flex", gap: 2, mb: 4 }}>
          <TextField
            label="Search Products"
            variant="outlined"
            size="small"
            sx={{
              flex: 1,
              backgroundColor: "#e0e0e0",
              "& .MuiOutlinedInput-root": { borderRadius: "10px" },
            }}
          />
          <FormControl size="small" sx={{ minWidth: 150, backgroundColor: "#e0e0e0", borderRadius: "10px" }}>
            <InputLabel id="category-label">Category</InputLabel>
            <Select labelId="category-label" value={category} onChange={handleCategoryChange}>
              <MenuItem value="electronics">Electronics</MenuItem>
              <MenuItem value="fashion">Fashion</MenuItem>
              <MenuItem value="home">Home</MenuItem>
              <MenuItem value="beauty">Beauty</MenuItem>
            </Select>
          </FormControl>
        </Box>

        {/* Products Grid */}
        <Grid container spacing={4}>
          {[...Array(8)].map((_, index) => (
            <Grid item xs={12} sm={6} md={4} lg={3} key={index}>
              <Card
                sx={{
                  borderRadius: 3,
                  boxShadow: "0 4px 8px rgba(0, 0, 0, 0.15)",
                  transition: "transform 0.3s",
                  backgroundColor: "#e0e0e0",
                  "&:hover": { transform: "scale(1.03)", boxShadow: "0 6px 12px rgba(0, 0, 0, 0.25)" },
                }}
              >
                <CardContent>
                  <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 1, color: "#1b1b2f" }}>
                    Product Name
                  </Typography>
                  <Typography variant="body2" sx={{ mb: 2, color: "#7f8c8d" }}>
                    $199.99
                  </Typography>
                  <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                    <IconButton>
                      <EditIcon sx={{ color: "#1f4068" }} />
                    </IconButton>
                    <IconButton>
                      <DeleteIcon sx={{ color: "#d9534f" }} />
                    </IconButton>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>

        {/* Pagination */}
        <Box sx={{ display: "flex", justifyContent: "center", mt: 4 }}>
          <Pagination count={5} color="primary" />
        </Box>
      </Container>
      <Footer />
    </>
  );
};

export default ProductPage;
